import { fetch } from "./fetch"; //引用fetch.js
import api from './url'; //引用url.js

//注册
export function register(url,data) {
  return fetch({
    //api.baseUrl 引用url.js里面的基础路径数据
    url: api.baseUrl + url,
    method: 'post',//请求方法
    data:data
  })
};

//是否登录
export function isLogin(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'get',
    params:data
  })
};

//未登录401跳转
export function noLogin(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'get',
    params:data
  })
};



