import { register,isLogin,noLogin } from '@/lib/axios/api';

export default {
  data () {
    let regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,       //英文特殊字符
        regEn1 = /[`~!@#$%^&*()_+<>?:"{},;'[\]]/im,       //英文特殊字符
        regCn = /[·！#￥（——）：；“”‘、，|《。》？、【】[\]]/im,//中文特殊字符
        regZh = /[\u4e00-\u9fa5]/ig                         //中文字符
    // 服务编号验证方法
    const validateNum = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入服务编号'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('服务号只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    // 服务标识验证方法
    const validateSign = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入服务标识'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn1.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('服务标识只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    // AccessToken方法
    const validateToken = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入AccessToken'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('AccessToken只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    // 回调地址验证方法
    const validateUri = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入回调地址'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn1.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('回调地址只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    // 项目组Leader验证方法
    const validateLeader = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入项目组Leader'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('项目组Leader工号只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    // 域名验证方法
    const validateDomainName = (rule, value, callback) => {
        if (value === '') {
            return callback(new Error('请输入域名'));
        }
        //模拟异步验证效果
        setTimeout(() => {
            if (regEn1.test(value) || regCn.test(value) || regZh.test(value)){
                callback(new Error('域名只能输入数字和字母'));
            } else {
                callback()
            }
        }, 500);
    };
    return {
      regCard:true, //注册卡和提示卡切换展示
      loading: false,  //按钮加载中
      formReg: {    //注册输入值
          ServiceNumber: '',//服务编号
          ServiceSign: '',  //服务标识
          AccessToken: '',  //AccessToken
          RedirectUri: '',  //回调地址
          TeamLeader: '',   //项目组Leader
          DomainName: ''    //域名
      },
      hintTitle:"",     //提示标题
      hintContent:"",   //提示内容
      hintTip:"",       //提示副内容
      // 整个regForm的验证规则
      ruleReg: {
          ServiceNumber: [
              { required: true, validator: validateNum }
          ],
          ServiceSign: [
              { required: true, validator: validateSign }
          ],
          AccessToken: [
              { required: true, validator: validateToken }
          ],
          RedirectUri: [
              { required: true, validator: validateUri }
          ],
          TeamLeader: [
              { required: true, validator: validateLeader }
          ],
          DomainName: [
              { required: true, validator: validateDomainName }
          ]
      }
    }
  },
  methods: {
    // 提交/保存按钮
    handleSubmit (name) {
        this.loading = true
        this.$refs[name].validate((valid) => {
            if (valid) {
                register('/OAuthManage/Save',this.formReg)
                .then((res) => {
                  console.log('成功',res)
                  if(res.data.Success){
                    this.$Message.success('注册成功!');
                    this.hintTitle = "注册成功";
                    this.hintContent = "这是您的秘钥：" + res.data.Data.ClientSecret + "。";
                    this.hintTip = "请妥善保管。若有遗失，请联系管理员获取。"
                  }else{
                    this.$Message.error('注册失败!');
                    this.hintTitle = "注册失败";
                    this.hintContent = res.data.Message || "系统错误，请联系管理员"
                  }
                  this.regCard = false
                })
                .catch((err) => {
                  console.log('失败',err)
                })
            } else {
                this.$Message.error('请输入正确的信息!');
                this.loading = false
            }
        })
    },
    // 取消/重置按钮
    handleReset (name) {
        this.$refs[name].resetFields();
    }
  },
  beforeCreate: function () {
    isLogin('/OAuthManage/Register')
    .then((res) => {
      console.log('成功',res)
    })
    .catch((err) => {
      console.log('失败',err)
      var str = JSON.stringify(err);
      if(str.includes('401')){ //'status' = "401" 未登录
          let thisUrl = window.location.href;
          thisUrl=thisUrl.replace(/\#/g,"~");
          noLogin('/Default/LoginUrl',{
            returnUrl:thisUrl
          })
          // this.$http({
          //   method: 'get',
          //   url: 'Default/LoginUrl?returnUrl=' + thisUrl,//传入本页地址跳转至登录，登录后回转
          // })
          .then((res) => {
            console.log('成功',res.data);
            window.location.href=res.data
          })
          .catch((err) => {
            this.$Message.error('sso登录失败');
          })
      }else {
        this.$Message.error(err);
      }
    })
  }
}



